import React from "react";

import { HeadFC, PageProps, graphql } from "gatsby";
import { useLocation } from "@reach/router";

import { IPostQueryResult } from "../types/post.type";

import Clock from "../assets/common/clock.svg";

import { LocalBusinessMetadata } from "../components/common/metadata";

import ContentContainer from "../components/common/contentContainer";
import Banner from "../components/common/banner";
import ShareButtons from "../components/posts/shareButtons";
import PostFooter from "../components/posts/postFooter";

const Post = ({
  data: {
    markdownRemark: {
      frontmatter,
      html,
      wordCount: { words },
    },
  },
}: PageProps<IPostQueryResult>) => {
  const { href: currentUrl } = useLocation();

  const { date, title, keywords } = frontmatter;

  const minutes = Math.floor(words / 200);
  const readingTime = minutes ? minutes : 1;

  return (
    <>
      <Banner
        heightClass="h-136"
        title="MAKE BLOGGING GREAT AGAIN"
        text="Well unknown facts for your tech savvy and entertainment"
        borderBottom
      />
      <div itemScope itemType="https://schema.org/Article" className="px-6">
        <ContentContainer className="flex-col max-w-6xl">
          <div className="w-full flex pt-14 pb-9 items-center">
            <span
              className={`inline text-2xl sm:text-3xl text-main-100 font-light pr-11`}
              itemProp="datePublished"
            >
              {date}
            </span>

            <Clock />

            <span className="inline text-xl font-light italic pl-4">
              Reading time: {readingTime}m.
            </span>
          </div>
          <h2
            className="text-main-100 text-4xl sm:text-5xl md:text-6xl font-semibold leading-[52px] sm:pb-10 md:pb-16"
            itemProp="headline"
          >
            {title}
          </h2>
          <div
            className="single-post text-main-100 font-medium text-2xl pb-10"
            dangerouslySetInnerHTML={{ __html: html }}
            itemProp="articleBody"
          />
          <div className="w-full flex flex-col md:flex-row justify-between items-center pb-24">
            {keywords ? (
              <div className="basis-full sm:basis-3/5 text-center md:text-start pb-10 md:pb-0">
                {keywords.map((hashtag) => (
                  <div
                    key={hashtag}
                    className="inline-block text-main-10 border-main-10 border-solid border rounded-full p-1 px-4 text-2xl m-2"
                    itemProp="keywords"
                  >
                    #{hashtag.toLowerCase()}
                  </div>
                ))}
              </div>
            ) : null}
            <div className="basis-full sm:basis-2/5 flex flex-row justify-end">
              <ShareButtons url={currentUrl} title={title} />
            </div>
          </div>
        </ContentContainer>
        <LocalBusinessMetadata itemProp="copyrightHolder" />
      </div>
      <PostFooter />
    </>
  );
};

export default Post;

export const Head: HeadFC<IPostQueryResult> = ({
  data: {
    markdownRemark: {
      frontmatter: { title },
    },
  },
}) => <title>CodeCave blog: {title}</title>;

export const query = graphql`
  query ($id: String) {
    markdownRemark(id: { eq: $id }) {
      frontmatter {
        date(formatString: "MMM DD, YYYY")
        title
        keywords
      }
      html
      wordCount {
        words
      }
    }
  }
`;
